    saveMetaData(){
      let that = this
      let find =false
      if(that.globalMetadataArray.length>0){
        for (let i1 = 0; i1 < that.globalMetadataArray.length; i1++) {
            let fields = that.globalMetadataArray[i1].fields
            for (let i2 = 0; i2 < fields.length; i2++) {
              let rowKey = Object.keys( fields[i2]).length
              if (rowKey!=4){//4key
                this.$message({
                  type: 'info',
                  message: '['+that.globalMetadataArray[i1].id +']'
                });
                find = true
                break
              }else{
                if (fields[i2].name==="" ||fields[i2].type===""||fields[i2].nullable==="" ){
                  this.$message({
                    type: 'info',
                    message: '['+that.globalMetadataArray[i1].id +'][nametypenullable]'
                  });
                  find = true
                  break
                }
              }
            }
            if (find===true){
              break
            }
        }
        if (find===false){
           that.metaDataManagerInfo.metaDataDialog = false
          console.info("that.globalMetadataArray:",that.globalMetadataArray)
        }

      }else{
        that.metaDataManagerInfo.metaDataDialog = false
      }

    },
