    //
    drawNode(e){
      let that = this
      if (that.canvasPro.publicScene!=null){
        if (that.canvasPro.currentNodeType!=null){
          that.canvasPro.currentMouseX = e.offsetX// e.pageX//X
          that.canvasPro.currentMouseY = e.offsetY//e.pageY//Y
          // console.info("canvasPro.currentMouseX:",that.canvasPro.currentMouseX)
          // console.info("canvasPro.currentMouseY:",that.canvasPro.currentMouseY)

          ////////node start///////
          // var node = new JTopo.Node(that.canvasPro.currentNodeType);
          // node.fillColor = '0,0,255';
          // // node.setLocation(409, 269);
          // //node.setImage('/static/bg.png', true);
          // node.setLocation(that.canvasPro.currentMouseX, that.canvasPro.currentMouseY);
          // node.setSize(this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height);//
          that.canvasPro.currentNodeModelPro.x=that.canvasPro.currentMouseX
          that.canvasPro.currentNodeModelPro.y=that.canvasPro.currentMouseY

          // console.info("that.globalNodeArray.length:",that.globalNodeArray.length)
          //clone
          let _nodePro_ = JSON.parse(JSON.stringify(that.canvasPro.currentNodePro));
          if  (that.globalNodeArray.length==0){
            that.canvasPro.currentNodeModelPro.sortId= 1
            that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType
            that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_"+1
            that.canvasPro.currentNodeModelPro.multiWay="0"

            _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)

          }else{
            //
            let tmp = that.findNodeTypeForArray(that.canvasPro.currentNodeType)
            if (tmp.length==0){
              that.canvasPro.currentNodeModelPro.sortId= 1
              that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType
              that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_"+1
              that.canvasPro.currentNodeModelPro.multiWay="0"

              _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)
            }else{
              //
              tmp = that.sortKey(tmp,"sortId")
              // console.info("that.globalNodeArray :",tmp ,"that.globalNodeArray[0].modelPro.id",tmp[0].modelPro.id)
              that.canvasPro.currentNodeModelPro.sortId =  tmp[0].modelPro.sortId+1
              that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType

              that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_" +  that.canvasPro.currentNodeModelPro.sortId
              that.canvasPro.currentNodeModelPro.multiWay="0"
              // console.info("that.canvasPro.currentNodeModelPro.id:",that.canvasPro.currentNodeModelPro.id)
              _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)

            }

          }

          var container = this.createContainer(that.canvasPro.currentMouseX,that.canvasPro.currentMouseY,
            this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height,
            that.canvasPro.currentNodeText,_nodePro_,that.canvasPro.currentNodeModelPro)
            // that.canvasPro.currentNodeText,that.canvasPro.currentNodePro,that.canvasPro.currentNodeModelPro)
          // that.canvasPro.publicScene.add(node);
          // that.canvasPro.publicScene.add(container);

          ////////node end///////
          that.globalNodeArray.push(container)//
          that.canvasPro.currentNodeType = null
          // console.info("that.globalNodeArray",that.globalNodeArray)
        }else{
          console.info('')
        }
      }else{
        console.info('')
      }
