    createContainer(x,y,w,h,text,nodePro,modelPro){
      var that = this;
      // var container = new JTopo.Container(text);
      var container = new JTopo.Container();
      container.textPosition = 'Middle_Center';
      container.fontColor = '100,255,0';
      container.font = '14pt ';
      let findSourceType = this.isInPutNode(modelPro.type)
      let findTargetType = this.isOutPutNode(modelPro.type)
      let findType = this.isTransformNode(modelPro.type)

      if (findSourceType==true){
        container.fillColor = '200,167,9';//
      }else if(findTargetType==true){

        // container.fillColor = '117,141,240';//
        container.fillColor = '0,230,230';//
      }else if(findType==true){
        container.fillColor = '4,198,33';//
      }else{
        container.fillColor = '104,198,33';//
      }

      container.borderColor = '0,0,0';//
      container.borderWidth = 1; // 

      // container.borderRadius = 30; // 
      container.alpha = 1;//
      container.setBound(x, y, w, h);
      container.nodePro = nodePro//
      container.modelPro = modelPro//

      // 10)
      // var flowLayout = JTopo.layout.FlowLayout(10, 10);
      // (43)
      var gridLayout = JTopo.layout.GridLayout(1, 1);
      //
      container.layout = gridLayout;
      let n1 = this.createNode(text,10,10,that.NODE_PROPERTIES.width,that.NODE_PROPERTIES.height,nodePro,modelPro);


      container.add(n1);
      //,
      n1.parentNode = container

      container.addEventListener("dbclick", function(event){
        // container.dragable = !container.dragable;//
        that.canvasPro.currentContainer = event.target;

        // console.info("nodePro"+JSON.stringify(that.canvasPro.currentContainer.nodePro));
        // console.info("modelPro"+JSON.stringify(that.canvasPro.currentContainer.modelPro));
        // that.$message({
        //   type: 'info',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   // iconClass:'el-icon-info',
        //   message: '<spand style="color:black;"> ID'+this.currentContainer.text +"</spand>"
        // });

      });

      container.addEventListener('click',function(event){
         // console.info("event.target.nodePro:",event.target.nodePro)
         // console.info("event.target.modelPro:",event.target.modelPro)
        //table
         that.nodeTableData = event.target.nodePro

      });
      // scene.add(container);
      that.canvasPro.publicScene.add(container);


      return container;
    },
