    /*
     ,
     @param sourceNodeModelPro modelPro
     @param targetNodeModelPro modelPro
     @return 
     */
    paintinglinkLogic (sourceNodeModelPro,targetNodeModelPro) {
      let that = this;
      // if (e.target != null && e.target instanceof window.JTopo.Node) {
      //   console.info("offsetX:",e.offsetX,"offsetY",e.offsetY,"text:",e.target.text)
      // }
      let sourceType = sourceNodeModelPro.type
      let sourceId = sourceNodeModelPro.id
      let sourceIsLink = sourceNodeModelPro.isLink
      //copystream,01copystream1transform10
      let sourceMultiWay = sourceNodeModelPro.multiWay
      let targetType = targetNodeModelPro.type
      let targetId = targetNodeModelPro.id
      let targetIsLink = targetNodeModelPro.isLink


      let findSType = 1
      let findTType = 1

      findSType = this.isInPutNodeExt(sourceType)
      findTType = this.isOutPutNodeExt(targetType)

      // console.info("findSType:",findSType,"findTType:",findTType)
      if (findSType==1){
        console.info("")
        return null
      }
      if (findTType==1){
        console.info("")
        return null
      }
      //
      if ((findSType!=1) && (findTType!=1)){
          if(findSType==2 && findTType==2) {//  
            if (sourceIsLink==true ){
              console.info("")
              return null
            } else if(sourceIsLink==false && targetIsLink==false){

              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("-")
              return null
            }

          }else if(findSType==2 && findTType==3){//  transform
            if (sourceIsLink==true ){
              console.info("[]")
              return null
            }else if(sourceIsLink==false && targetIsLink==false){
              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("[][transform]-")
              return null
            }
          }else if(findSType==3 && findTType==3){//transform  transform
            if (targetIsLink==true ){
              console.info("[transform][transform]")
              return null
            }else if(sourceMultiWay=="0" && targetIsLink==false && sourceIsLink==true){
              // -> TRANSFROM -> TRANSFROM -> TRANSFROM ->   TRANSFROM ->   TRANSFROM -> TRANSFROM
              let _link_ = that.drawLink(sourceId,targetId,"TRANSFORM")
              //sourceNodeModelPro.multiWay="1"
              that.setMultiWayForNodeArray(sourceId)
              //TRANSFROMisLink
              that.setIsLinkForNodeArray(targetId)
              return _link_
            }else{
              console.info("[transform][transform]-")
              return null
            }
          }else if(findSType==3 && findTType==2){//transform  
            if (targetIsLink==true ){
              console.info("[transform][]")
              return null
            }else  if  (sourceMultiWay=="0" && targetIsLink==false ){
              let _link_ = that.drawLink(sourceId,targetId,"TRANSFORM")
              //sourceNodeModelPro.multiWay="1"
              that.setMultiWayForNodeArray(sourceId)
              return _link_
            }else{
              console.info("[transform][]-")
              return null
            }
          }else if(findSType==2 && findTType==4){//  copystream
            if (sourceIsLink==true ){
              console.info("[]")
              return null
            }else if(sourceIsLink==false && targetIsLink==false){
              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("[][copystream]-")
              return null
            }
          }else if(findSType==4 && findTType==2) {//copystream  
            if (targetIsLink==true ){
              console.info("[copystream][]")
              return null
            }else if(targetIsLink==false ){
              //copystreamtransform
              return that.drawLink(sourceId,targetId,"COPY")
            }
          }else{
            console.info("[" + sourceType + "][" + targetType + "]")
            return null
          }
      }
    },
    /*
    * @param _edgeType_ STANDARD | TRANSFORM  | COPY
    * */
    drawLink(sourceId,targetId,_edgeType_){
      let that = this
      let nodeTo = that.findNodeIdForArray(targetId);
      let link = new JTopo.Link(that.paintingLinkPro.sourceNode.parentNode, nodeTo );//
      // let link = new JTopo.Link(that.paintingLinkPro.sourceNode.parentNode, nodeTo );//
      // let link = new JTopo.CurveLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo);//
      // let link = new JTopo.FlexionalLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo, "");//
      // let link = new JTopo.FoldLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo, "");//
      link.toArrowSize = 30;
      // link.fromArrowSize = 10;
      link.arrowsRadius = 10; //
      link.strokeColor = '0,0,255';//
      link.bundleOffset = 60; // 
      link.bundleGap = 20; // 
      link.zIndex = 0//
      that.canvasPro.publicScene.add(link)
      // link.modelPro = {"from":sourceId,"to":targetId,"type":"STANDARD","order":"0","metadata":""}
      if (_edgeType_=="STANDARD"){
        link.modelPro = {"from":sourceId,"to":targetId,"type":_edgeType_,"order":"0","metadata":""}

        link.nodePro = [
          {"k":"from","flag":"text","v":sourceId,"edit":"false","show":"true"},
          {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
          {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
          {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
          {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
        ]
      }else if(_edgeType_=="TRANSFORM"){
        link.modelPro = {"from":sourceId+":0","to":targetId,"type":_edgeType_,"order":"0","metadata":""}

        link.nodePro = [
          {"k":"from","flag":"text","v":sourceId+":0","edit":"false","show":"true"},
          {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
          {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
          {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
          {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
        ]

      }else if(_edgeType_=="COPY"){
        ///////////////

        //

        let tmp = that.findNodeIdForLineCopyStreamArray(sourceId)
        if (tmp.length==0){
          link.modelPro = {"from":sourceId+":1","to":targetId,"type":_edgeType_,"order":"0","metadata":""}

          link.nodePro = [
            {"k":"from","flag":"text","v":sourceId+":1","edit":"false","show":"true"},
            {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
          ]
          that.globalLineCopyStreamArray.push({"id":sourceId+":1","sortId":1})
          // console.info("that.globalLineCopyStreamArray:",that.globalLineCopyStreamArray)
        }else{
          // console.info("tmp:",tmp)
          //
          tmp = that.sortKeyForCopyStream(tmp,"sortId")
          let sortId = tmp[0].sortId + 1
          let id =  sourceId +":"+ sortId
          link.modelPro = {"from":id,"to":targetId,"type":_edgeType_,"order":"0","metadata":""}

          link.nodePro = [
            {"k":"from","flag":"text","v":id,"edit":"false","show":"true"},
            {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
          ]

          that.globalLineCopyStreamArray.push({"id":id ,"sortId":sortId})
        }
        ////////////
      }


      ///
      //
      if  (that.globalLineArray.length==0){
        link.modelPro.sortId = 1
        link.modelPro.id = "LINE_" + _edgeType_ + "_" + 1
        // link.type = "STANDARD"
        link.type = _edgeType_
      }else{
        //
        // let tmp = that.findLineTypeForArray("STANDARD")
        let tmp = that.findLineTypeForArray(_edgeType_)
        if (tmp.length==0){
          link.modelPro.sortId = 1
          link.modelPro.id = "LINE_"  + _edgeType_ + "_" + 1
          // link.type = "STANDARD"
          link.type = _edgeType_
        }else{
          //
          tmp = that.sortKey(tmp,"sortId")
          link.modelPro.sortId = tmp[0].modelPro.sortId + 1
          link.modelPro.id = "LINE_"  + _edgeType_ + "_" +  link.modelPro.sortId
          // link.type = "STANDARD"
          link.type = _edgeType_
        }

      }
      ///
      //COPY_STREAMisLinkCOPY_STREAMisLink,COPY_STREAMCOPY_STREAM
      if (_edgeType_!="COPY" && _edgeType_!="TRANSFORM"){
        that.setIsLinkForNodeArray(sourceId)
      }

      that.setIsLinkForNodeArray(targetId)
      that.globalLineArray.push(link)
      // console.info("drawLink::that.globalLineArray:",that.globalLineArray)
      link.addEventListener('click',function(event){
        // console.info("event.target.nodePro:",event.target.nodePro)
        // console.info("event.target.modelPro:",event.target.modelPro)
        //table
        that.nodeTableData = event.target.nodePro

      });
      return link
    },
    isInPutNode(sourceType){
      let findSourceType = false
      for (let i1 = 0; i1 <this.INPUT_NODE_TYPE.length ; i1++) {
        if (this.INPUT_NODE_TYPE[i1].indexOf(sourceType)!=-1){
          findSourceType = true
          // console.info("",findSourceType)
          break
        }
      }
      return findSourceType
    },
    isInPutNodeExt(sourceType){
      let findSourceType = 1
      for (let i1 = 0; i1 <this.INPUT_NODE_TYPE.length ; i1++) {
        if (this.INPUT_NODE_TYPE[i1].indexOf(sourceType)!=-1){
          findSourceType = 2//
          break
        }
      }
      if (findSourceType==1){
        for (let i1 = 0; i1 <this.TRANSFORM_NODE_TYPE.length ; i1++) {
          if (this.TRANSFORM_NODE_TYPE[i1].indexOf(sourceType)!=-1){
            findSourceType = 3//transform
            break
          }
        }
      }
      if (findSourceType==1){
        for (let i1 = 0; i1 <this.COPYSTREAM_NODE_TYPE.length ; i1++) {
          if (this.COPYSTREAM_NODE_TYPE[i1].indexOf(sourceType)!=-1){
            findSourceType = 4//copystream
            break
          }
        }
      }

      return findSourceType
    },
