    //json
    sortKey(array,key){
      return array.sort(function(a,b){
        if(!a.modelPro.hasOwnProperty(key)){
          a.modelPro[key]=0;
        }
        if(!b.modelPro.hasOwnProperty(key)){
          b.modelPro[key]=0;
        }
        var x = parseInt(a.modelPro[key]);
        var y = parseInt(b.modelPro[key]);
        // return ((x<y)?-1:(x>y)?1:0)
        return ((x<y)?1:(x>y)?-1:0)


      })
    },
    sortKey2(array,key){
      return array.sort(function(a,b){
        if(!a.hasOwnProperty(key)){
          a[key]=0;
        }
        if(!b.hasOwnProperty(key)){
          b[key]=0;
        }
        var x = parseInt(a[key]);
        var y = parseInt(b[key]);
        // return ((x<y)?-1:(x>y)?1:0)
        return ((x<y)?1:(x>y)?-1:0)


      })
    },
    //copyStreamsortId
    sortKeyForCopyStream(array,key){
      return array.sort(function(a,b){
        if(!a.hasOwnProperty(key)){
          a[key]=0;
        }
        if(!b.hasOwnProperty(key)){
          b[key]=0;
        }
        var x = parseInt(a[key]);
        var y = parseInt(b[key]);
        // return ((x<y)?-1:(x>y)?1:0)//asc
        return ((x<y)?1:(x>y)?-1:0)//desc


      })
    },
