      for (let i1 = 0; i1 <nodePro.length ; i1++) {
         if (nodePro[i1].k==key) {
             // Object.defineProperty(nodePro[i1], "v", {
             //   value: value
             // })
              nodePro[i1].v=value
             find = true
           // console.info("find:",find)
             break
         }
      }
      if (find==false){
        nodePro.push({"k":key,"v":value,"edit":"false","show":"false"})
      }
      // console.info("synchNodeProFromKV:",nodePro)
      return nodePro
