    //xml
    loadXMLDrawNode(){
      let that = this
      let currentNodeText = "";
      // let xmlStr = this.$refs["graphXml"].innerText;
      let xmlStr = this.$refs["graphXml"].value;
      // let xmlStr =""

      // console.info("xmlStr:",xmlStr)
      let xmlDom = etlTools.createXmlDom();
      xmlDom = etlTools.parseStringToXML(xmlDom,xmlStr);
      let nodeArray = etlTools.getNodeArrayFromXML(xmlDom,"Node");
      let lineArray = etlTools.getNodeArrayFromXML(xmlDom,"Line");
      let comment = etlTools.getNodeArrayFromXML(xmlDom,"Comment");
      let connectionArray = etlTools.getNodeArrayFromXML(xmlDom,"Connection");
      let metadataArray = etlTools.getNodeArrayFromXML(xmlDom,"Metadata");
      //
      for (let i1 = 0; i1 <nodeArray.length; i1++) {
        // console.info("Node:",nodeArray[i1])
        let nodePro = [];
        let modelPro = {};




        // console.info("getAttributeNames:",nodeArray[i1].getAttributeNames())
        //modelPro
        for (let i2 = 0; i2 <nodeArray[i1].getAttributeNames().length ; i2++) {
          let key = nodeArray[i1].getAttributeNames()[i2];
          let value = nodeArray[i1].getAttribute(key)
          // console.info(key,value)
          //modelPro
          modelPro[key] = value;
        }

        // console.info("node modelPro",modelPro)
        //nodePro
        if (modelPro.type===this.nodeType.DB_INPUT_TABLE){
          nodePro = this.dbInputTable
          currentNodeText = ''
        }else if (modelPro.type===this.nodeType.DB_OUTPUT_TABLE){
          nodePro = this.dbOutputTable
          currentNodeText = ''
        }else if (modelPro.type===this.nodeType.XLS_READER){
          nodePro = this.xlsReader
          currentNodeText = 'EXCEL'
        }else if (modelPro.type===this.nodeType.XLS_WRITER){
          nodePro = this.xlsWriter
          currentNodeText = 'EXCEL'
        }else if (modelPro.type===this.nodeType.COPY_STREAM){
          nodePro = this.copyStream
          currentNodeText = ''
        }else if (modelPro.type===this.nodeType.TRANSFORM){
          nodePro = this.transform
          currentNodeText = ''
        }else if (modelPro.type===this.nodeType.DB_EXECUTE_TABLE){
          nodePro = this.dbExcuteTable
          currentNodeText = 'SQL'
        }else if (modelPro.type===this.nodeType.CSV_READER){
          nodePro = this.csvReader
          currentNodeText = 'CSV'
        }else if (modelPro.type===this.nodeType.REDIS_READER){
          nodePro = this.redisReader
          currentNodeText = 'Redis'
        }else if (modelPro.type===this.nodeType.EXECUTE_SHELL){
          nodePro = this.excuteShell
          currentNodeText = ''
        }else if (modelPro.type===this.nodeType.CUSTOM_READER_WRITER){
          nodePro = this.customReaderWriter
          currentNodeText = ''
        }else if (modelPro.type==="MQ_CONSUMER" && modelPro.flag=== "ROCKETMQ"){
          nodePro = this.rocketMqConsumer
          currentNodeText = 'RocketMQ'
        }else if (modelPro.type==="MQ_CONSUMER" && modelPro.flag=== "KAFKA"){
          nodePro = this.kafkaConsumer
          currentNodeText = 'Kafka'
        }else if (modelPro.type===this.nodeType.OUTPUT_TRASH){
          nodePro = this.outputTrash
          currentNodeText = ''
        }else if (modelPro.type=== this.nodeType.REDIS_WRITER){
          nodePro = this.redisWriter
          currentNodeText = 'Redis'
        }else if (modelPro.type=== "MQ_PRODUCER"  && modelPro.flag=== "ROCKETMQ"){
          nodePro = this.rocketMqProducer
          currentNodeText = 'RocketMQ'
        }else if (modelPro.type=== "MQ_PRODUCER"  && modelPro.flag=== "KAFKA"){
