        let container = that.createContainer(parseInt(modelPro.x),parseInt(modelPro.y),
          this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height,
          currentNodeText,nodePro,modelPro)


        that.globalNodeArray.push(container)//
        // console.info("XMLthat.globalNodeArray",that.globalNodeArray)
      }
      //
      for (let i4 = 0; i4 < lineArray.length; i4++) {
        // console.info("Line:",lineArray[i4])
        let nodePro = [];
        let modelPro = {};
        let fromNodeId = "";
        let toNodeId = "";
        let type = "";
        let fromNode = null;
        let toNode = null;
        //modelPro
        for (let i2 = 0; i2 <lineArray[i4].getAttributeNames().length ; i2++) {
          let key = lineArray[i4].getAttributeNames()[i2];
          let value = lineArray[i4].getAttribute(key)
          // console.info(key,value)
          //modelPro
          modelPro[key] = value;
        }
        // console.info("line modelPro:",modelPro)
        // console.info("line modelPro.length:",Object.keys(modelPro).length)
        if (Object.keys(modelPro).length>0){
          fromNodeId = modelPro['from']
          toNodeId = modelPro['to']
          type = modelPro['type']
          nodePro = [
            {"k":"from","flag":"text","v":modelPro['from'],"edit":"false","show":"true"},
            {"k":"to","flag":"text","v":modelPro['to'],"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":modelPro['type'],"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":modelPro['order'],"edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":modelPro['metadata'],"edit":"true","show":"true"},
          ]
          // console.info("fromNodeId_1:",fromNodeId)
          if (type==="STANDARD"){
             fromNode = that.findNodeIdForArray(fromNodeId);
             toNode = that.findNodeIdForArray(toNodeId);
          }else if (type==="COPY" || type==="TRANSFORM"){
            //COPY_STREAM_1:1  TRANSFORM_1:0
            let pos=fromNodeId.indexOf(":")
            if(pos!=-1){
              fromNodeId = fromNodeId.substr(0,pos)
            }
             fromNode = that.findNodeIdForArray(fromNodeId);
             toNode = that.findNodeIdForArray(toNodeId);
          }
          // console.info("fromNodeId_2:",fromNodeId)

          if(fromNode!=null && toNode!=null){
            let link = new JTopo.Link(fromNode, toNode );//

            link.toArrowSize = 30;
            // link.fromArrowSize = 10;
            link.arrowsRadius = 10; //
            link.strokeColor = '0,0,255';//
            link.bundleOffset = 60; // 
            link.bundleGap = 20; // 
            link.zIndex = 0//
            link.nodePro = nodePro;
            link.modelPro = modelPro;
            that.canvasPro.publicScene.add(link)
            that.globalLineArray.push(link)
            // console.info("loadXMLDrawNode::that.globalLineArray:",that.globalLineArray)
            link.addEventListener('click',function(event){
              // console.info("event.target.nodePro:",event.target.nodePro)
              // console.info("event.target.modelPro:",event.target.modelPro)
              //table
              that.nodeTableData = event.target.nodePro

            });
          }


        }





      }
      //Comment that.globalLineCopyStreamArray
      //[{"id":"COPY_STREAM_1:1","sortId":1},{"id":"COPY_STREAM_1:2","sortId":2}]
      if(comment.length>0) {
        // console.info("Comment:", comment[0])
        let cdata = comment[0].childNodes[0].wholeText.trim()
        let lineCopyStreamArray = JSON.parse(cdata)
        for (let i1 = 0; i1 <lineCopyStreamArray.length ; i1++) {
          that.globalLineCopyStreamArray.push(lineCopyStreamArray[i1])
        }
      }

      //Connection that.globalDbConnectArray
      for (let i1 = 0; i1 <connectionArray.length; i1++) {
        // console.info("Connection:", connectionArray[i1])
        let tmpObj = {};
        for (let i2 = 0; i2 <connectionArray[i1].getAttributeNames().length ; i2++) {
          let key = connectionArray[i1].getAttributeNames()[i2];
          let value = connectionArray[i1].getAttribute(key)
          // console.info(key,value)
          tmpObj[key] = value;
        }
        that.globalDbConnectArray.push(tmpObj)
      }
      //Metadata,that.globalMetadataArray
      for (let i1 = 0; i1 <metadataArray.length; i1++) {
        // console.info("Metadata:", metadataArray[i1])
        let fieldsNode = metadataArray[i1].getElementsByTagName("Field");
        let fields = []
        for (let i3 = 0; i3 < fieldsNode.length; i3++) {
          let field = {}
          for (let i5 = 0; i5 <fieldsNode[i3].getAttributeNames().length ; i5++) {
            let key = fieldsNode[i3].getAttributeNames()[i5];
            let value = fieldsNode[i3].getAttribute(key)
            field[key] = value;
          }
          fields.push(field);

        }
        let tmpObj2 = {};
        for (let i2 = 0; i2 <metadataArray[i1].getAttributeNames().length ; i2++) {
          let key = metadataArray[i1].getAttributeNames()[i2];
          let value = metadataArray[i1].getAttribute(key)
          // console.info(key,value)
          tmpObj2[key] = value;


        }
        tmpObj2['fields'] = fields;
        that.globalMetadataArray.push(tmpObj2)
      }
    }
  },

