<template>
  <div >
<!--    <a href="#"  class="btn btn-primary" @click="callParentMethod();" title="">{{ msg }}</a>-->
<!--    <router-link :to="{ name: 'form1', params: { userId: 123 }}"></router-link>-->
<!--    <router-link :to="{ name: 'form2', params: { userId: 123 }}"></router-link>-->

    <router-link :to="{ path: '/index/views/form1', params: { userId: 123 }}"></router-link>
    <router-link :to="{ path: '/index/views/form2', params: { userId: 456 }}"></router-link>

  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

// import rightContent from "./RightContent";
export default {
  name: 'leftTree',
  //
  props: {
    pri_name: {
      type: String,
      default: ''
    },
    pri_detail:{
      type:Object,
      default:function() {
          return{
            name1:'',
            alias:''
          }

      }
    }
  },
  methods: {
    created: function() {

      console.info('LeftTree.vue created userInfo:'+JSON.stringify(this.customGlobal.userInfo));

    },
    // components: {
    //   form1,
    //   form2
    //
    // },
    getChildDetail: function(){
      this.$data.height = Math.round(Math.random() *100)/100;
      var d1 = "pri_detail:"+JSON.stringify( this.pri_detail ) +",msg:"+this.msg +",height:" + this.$data.height +","+ new Date().toLocaleTimeString();
      return d1;
    },
    callParentMethod:function(){
      this.msg = ""+ Math.floor(Math.random() * 10 + 1);
      //12
      this.$emit("parent_method",[{"key1":"value1","key2":"value2"},{"key1_1":"value1_1","key2_1":"value2_1"}]);
      //this.$emit("parent_method","a","b");
      console.info("this.$route.params:"+ JSON.stringify( this.$route.params)+",$route.name:"+this.$route.name );
      this.$router.push({name:'rightContent',params:{'method':'list'}});
      // this.$router.push({path:'/right'});
    }
  },
  //
  data:function() {
    return {
      msg: '',
      height:179.08
    }

  },
  beforeRouteEnter (to, from, next) {//
    console.info("leftTreeto:"+to.name+",from:"+from);
    next();
  },
  beforeRouteLeave(to, from, next){//
    var res=confirm("?");
    if(res==true){
      next();
    }else{
      next(false);
    }

  },
  beforeRouteUpdate (to, from, next) {//
    console.info("leftTreeto:"+JSON.stringify(to)+",from:"+from+",next:"+  next );
    next();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



